import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Articles from './Articles'

import './App.css';

import search from './images/Search.svg';

const NewsAPI = require('newsapi');
const newsapi = new NewsAPI('c7df2a5b43e34e1ab866ad492e8834ae');

class App extends Component {
  constructor() {
    super();
    this.state = {
      articles: [],
      q: '',
      sources: '',
      placeholder: ''
    };
  }

  componentDidMount() {
    newsapi.v2.everything({
        sources: 'cnn, fox-news',
        language: 'en'
    })
    .then(data => {
      console.log(data);
      this.setState({
        articles: data.articles
      })
    });
  }

  handleChange(e){
    this.setState({q: e.target.value});
  }

  handleClick(e){
    e.preventDefault();
    newsapi.v2.everything({
        q: this.state.q,
        sources: 'cnn, fox-news',
        language: 'en'
    })
    .then(data => {
      this.setState({
        articles: data.articles
      })
    });
  }

  sideSwitch() {
    if (this.state.sources === 'cnn' || this.state.sources === '') {
      this.setState({
        sources: 'fox-news'
      });
    } else if (this.state.sources === 'fox-news' || this.state.sources === '') {
      this.setState({
        sources: 'cnn'
      });
    }
  }

  render() {
    let articles = this.state.articles;
    console.log(articles);
    if (this.state.sources === 'cnn') {
      articles = this.state.articles.filter((article) => article.source.id === 'cnn' );
    } else if (this.state.sources === 'fox-news') {
      articles = this.state.articles.filter((article) => article.source.id === 'fox-news');
    }

    return (
      <main>
        <header>
          <div className='sides'>
            <span
              className='left'
              onClick={() => this.sideSwitch()}
              >Left &larr;</span>
            <Link to='/'>
              <h1>SideNews</h1>
            </Link>
            <span
              className='right'
              onClick={() => this.sideSwitch()}
              >&rarr; Right</span>
          </div>
          <form>
            <input
              placeholder="Search for a topic"
              text={this.state.q}
              value={this.state.q}
              onChange={(e) => this.handleChange(e)}
              />
            <button
              className='search'
              onClick={(e) => this.handleClick(e)}
              ><img src={search} alt="search button"/>
            </button>
          </form>
          <div className='divider'></div>
        </header>
        <section className='source'>
          <Articles articles={articles}/>
        </section>
      </main>
    )
  }
}

export default App;
